@import "sass/index";

$moveButtonWidth: 43px;
$controlButtonColor: #ceb3ff;
$controlButtonHoverColor: rgb(172, 128, 252);

.root {
}

.editor {
  display: flex;
  flex-direction: column;
  align-items: center;

  background: transparent;
  width: 99%;
}
.cropper {
  max-height: 300px;
  max-width: 100%;
  height: max-content;
  width: max-content;
}
.previewImage {
  border-radius: 50%;
  max-height: 200px;
  max-width: 200px;
  object-fit: cover;
}
.disableImage {
  display: none !important;
}

.panel {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  padding: 15px;
  margin-top: 20px;

  background: $m_semiDarkPurple;

  border-radius: 5px;
  position: relative;

  @media (max-width: 450px) {
    grid-template-columns: 1fr;
  }
}
.buttonsWrapper {
  display: grid;
  grid-template-columns: min-content;
  grid-template-rows: repeat(auto-fill, minmax(35px, 2fr));
  grid-gap: 10px;
  gap: 10px;

  & button {
    background: $controlButtonColor;

    &:hover,
    &:active,
    &:focus {
      background: $controlButtonHoverColor;
    }
  }

  @media (max-width: 450px) {
    justify-content: center;
    margin-bottom: 30px;
  }
}
.buttonBox {
  display: flex;
  gap: 10px;
}
.movementBox {
  grid-area: 2 / 1 / 4;
  position: relative;
  min-height: 90px;
}

.moveButtons {
  position: absolute;
  height: 36px;
  min-width: $moveButtonWidth;
  width: $moveButtonWidth;
}
.buttonUp {
  top: 0;
  left: 50%;
  margin-left: -($moveButtonWidth / 2);
}
.buttonBottom {
  bottom: 0;
  left: 50%;
  margin-left: -($moveButtonWidth / 2);
}
.buttonLeft {
  left: 0;
  top: 50%;
  margin-top: -(36px / 2);
}
.buttonRight {
  right: 0;
  top: 50%;
  margin-top: -(36px / 2);
}

.imgWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 20px;

  position: relative;
}
.text {
  margin: 0;
  position: absolute;
  top: 0;
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
}
