.root {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 40px;
}

.text {
  font-size: 20px;
  color: white;
  line-height: 24px;
}

.switchBoxRoot {
  margin-top: 0px;
}

.switchThumb {
  background-color: #2e2b4a;
  height: 14px;
  width: 28px;
  background-color: #161425;
  // margin-bottom: -4px;
}

.switchToggle {
  background: white;
  width: 20px;
  height: 20px;
}

.switchRoot {
  margin-right: 12px;
  margin-left: 12px;
}

.switchText {
  color: #ffffff;
  transition: opacity, 0.4s;
  letter-spacing: 1px;
  font-weight: bold;
  text-transform: uppercase;
}
