@import "sass/index";

.wrapper {
  padding-top: 42px;
  padding-bottom: 62px;
  display: flex;
  justify-content: center;
}
.innerWrapper {
  width: 100%;
  max-width: 300px;
}
.title {
  font-weight: bold;
  font-size: 24px;
  color: white;
  letter-spacing: 0;
  line-height: 24px;
}
.errorWrapper {
  padding: spacing(1.5);
}

.divider {
  background-color: rgba(white, 0.1);
  margin-top: 24px;
  flex: 1;
}
