.root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  height: max-content;
  padding: 10px;
}
.contentBox {
  position: relative;
  width: 101%;
  height: 0;
  padding-bottom: 15px;
}
.content {
  transition: transform 0.5s;
  transform-origin: top center;
  position: absolute;
  top: 0;
  width: 100%;
  height: max-content;
}

.imgHide {
  transform: scaleY(0);
}
.editor {
  transform-origin: bottom center;
  top: auto;
  bottom: 0;
}

.button {
  background: rgba(255, 255, 255, 0.1) !important;
  color: white;
  margin-top: 10px;

  &:hover {
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 20%),
      0px 4px 5px 0px rgba(0, 0, 0, 14%), 0px 1px 10px 0px rgba(0, 0, 0, 12%);
    background: #d5d5d5;
    color: black;
  }
}
