@import "sass/index";

.wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
}
.button {
  height: 40px;
  background: rgba(white, 0.1) !important;
  color: white !important;
  padding: 6px 10px;

  transition: color $defaultTransition, background-color $defaultTransition,
    box-shadow $defaultTransition;

  &:hover {
    background: rgba(white, 0.75) !important;

    & p {
      color: #090c16;
    }
    & svg {
      opacity: 1;
    }
    & g path,
    circle,
    rect {
      stroke: #090c16 !important;
    }
    & :global g#shield > path:nth-child(2n) {
      fill: #090c16 !important;
    }
  }
}
.selectedButton {
  background: white !important;

  & p {
    color: #090c16;
  }
  & svg {
    opacity: 1;
  }
  & g > path,
  & circle,
  & rect {
    stroke: #090c16 !important;
  }
  & :global g#shield > path:nth-child(2n) {
    fill: #090c16 !important;
  }
}
.icon {
  opacity: 0.5;

  & g > path,
  & circle,
  & rect {
    stroke: white;
  }
}
.iconWrapper {
  height: 21px;
  width: 21px;
}
.label {
  margin-left: 10px;
  font-size: 12px;
  font-weight: bold;
  color: rgba(white, 0.5);
  letter-spacing: 0.86px;
  white-space: nowrap;
}
