@import "sass/index";

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 24px;
}
.row {
  display: flex;
  flex-direction: row;
  gap: 24px;
  flex-wrap: wrap;
}
.rowItem {
  display: flex;
  flex-direction: column;
  width: calc(100% - 184px);
}
.submitButton {
  max-width: 300px;
}
.qrCodeWrapper {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 160px;
  height: 160px;

  background: white;
  border-radius: spacing(1);
}
.qr {
  width: 140px;
  height: 140px;
}
.text {
  font-family: Helvetica;
  font-size: 16px;
  color: rgba(white, 0.5);
  letter-spacing: 0;
  line-height: 23px;
}
.codeInputWrapper {
  margin-top: 20px;
}
.warningBlock {
  display: flex;
  flex-direction: row;
  gap: 16px;
  padding: 12px;
  margin-top: 16px;
  background: rgba(white, 0.1);
  border-radius: 6px;
}
.warningIcon {
  width: 41px;
  height: 41px;
}
.warning {
  font-family: Helvetica;
  font-size: 13px;
  color: rgba(white, 0.5);
  letter-spacing: 0;
  line-height: 17px;
}
