@import "sass/index";

.root {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
.statusWrapper {
  position: absolute;

  background: black;
  opacity: 0.5;

  width: 100%;
  height: 100%;
}

.okIcon {
  color: $m_primary-main;
  opacity: 0.8;
  height: 75%;
  width: 75%;
}
