.root {
  display: block;
  height: 100%;
  margin: 0 auto;
  width: fit-content;
}

.label {
  display: flex;
  justify-content: center;
  position: relative;

  width: 160px;
  height: 160px;
  margin: 40px auto 16px;
  color: #fff;

  & > svg {
    transition: transform 0.5s;
    font-size: 3rem;
  }

  &:hover {
    & > svg {
      transform: scale(1.1);
    }
  }
}
.drag {
}
.loaderInput {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 1px solid;
  opacity: 0;

  cursor: pointer;
}

.loadedImage {
  max-width: 100%;
  max-height: 300px;
  margin: 0 auto;
  display: block;
}
.loadedImageWrapper {
  position: relative;
  margin: 0 auto;
}
.hideImage {
  display: none;
}
